/*global chrome*/

import "./index.css";
import ReactGA from "react-ga";
import mochiSave from "../../assets/Mochi-save-word.png";
import mochiNoti from "../../assets/Mochi-thong-bao.png";
import mochiTDV from "../../assets/Mochi-meomeo2.png";
import mochiOver from "../../assets/Mochi-chi-dan.png";
import PrimaryButton from "../commons/PrimaryButton";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { wordOperations } from "../../state/modules/word";
import { Col, Row } from "react-bootstrap";
import MainModal from "../commons/MainModal";
const trackingURL = process.env.REACT_APP_TRACKING_URL;
function NotifyModal({ type, setShowModal }) {
  // ReactGA.initialize("UA-150440250-2",{
  //   gaOptions: { cookieDomain: "none" },
  // });
  // const ga = ReactGA.ga();
  // ga("set","checkProtocolTask",function () {
  //   /* nothing */
  // });

  const [overLoad, setOverLoad] = useState(false);

  const accountState = useSelector((state) => state.account);

  const { expiredDay, statistic, reviewCount, reviewTime, email } = accountState;
  const reviewTimeObject = new Date(reviewTime);
  const now = new Date();

  const isBeforeTDV = reviewTimeObject.getTime() - now.getTime() > 0;

  const dispatch = useDispatch();
  const handleOpenWebsite = async () => {
    setShowModal("");
    window.open("https://learn.mochidemy.com/?source=extension&category=extension_browser&action=click_button&label=add_new_words", "_blank");
    // const useIp = await getIp();
    // await fetch(
    //   `${trackingURL}?action=newTab_open_website&ip=${useIp}&email=${email}`
    // )
    //   .then((response) => response.json())
    //   .then((data) => {
    //     if (data.result == "success") {
    //       console.log({ data });
    //     }
    //   });
  };
  const handleUpgrade = async () => {
    chrome.runtime.sendMessage("checkClickUpgrade");
    setShowModal("");
    window.open("https://mochidemy.com/extension?source=extension&category=extension_browser&action=click_button&label=upgrade_premium");
  };

  return (
    <>
      {/* Lu t thnh cng - Trc thi im vng */}
      {isBeforeTDV && (
        <MainModal
          show={type == "normal"}
          mascot={mochiTDV}
          handleClose={() => setShowModal("")}
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bn  lu t thnh cng.</p>
                <p className="notify-save-word">
                  ng qun n tp t vo Thi im Vng  tng hiu qu ghi
                  nh
                </p>
                <PrimaryButton
                  onClick={() => {
                    setShowModal("");
                  }}
                  className="btn_save_notify"
                >
                  Mnh  hiu
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
      {/* Lu t thnh cng - Sau thi im vng */}
      {!isBeforeTDV && (
        <MainModal
          show={type == "normal"}
          mascot={mochiSave}
          handleClose={() => setShowModal("")}
          className="modal_notify_save_over_TDV"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bn  lu t thnh cng.</p>
                <p className="notify-save-word">
                  ng nhp vo website v m tab S TAY  xem t vng va lu
                </p>
                <PrimaryButton
                  onClick={handleOpenWebsite}
                  className="btn_save_notify"
                >
                  M website ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}

      <MainModal
        show={type == "free-over-50lv1"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        {/* Free user */}

        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bn ang c hn 50 t vng level 1. n tp ngay  lu thm t
                vo s tay
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                n tp ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      <MainModal
        show={type == "paid-over-100-lv1"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bn ang c hn 100 t vng level 1.
              </p>
              <p className="notify-save-word">
                n tp ngay  lu thm t vo s tay
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                n tp ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      <MainModal
        show={type == "paid-over-100"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bn ang c hn 100 t cn n tp.
              </p>
              <p className="notify-save-word">
                n tp ngay  lu thm t vo s tay
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                n tp ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      <MainModal
        show={type == "paid-save-under-100"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bn  lu t thnh cng v ang c {reviewCount} t n gi n
                tp.
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                n tp ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>
      <MainModal
        show={type == "free-review"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={{ span: 8, offset: 2 }}>
            <div>
              <p className="notify-save-word">
                Bn  lu t thnh cng v ang c {reviewCount} t n gi n
                tp.
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                n tp ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      {!isBeforeTDV && (
        <MainModal
          show={type == "normal"}
          mascot={mochiSave}
          handleClose={() => setShowModal("")}
          mascotClass="save_over_TDV"
          className="modal_notify_save_over_TDV"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bn  lu t thnh cng.</p>
                <p className="notify-save-word">
                  ng nhp vo website v m tab S TAY  xem t vng va lu
                </p>
                <PrimaryButton
                  onClick={handleOpenWebsite}
                  className="btn_save_notify"
                >
                  M website ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
      {!expiredDay && (
        <MainModal
          show={type == "overload"}
          mascot={mochiNoti}
          handleClose={() => setShowModal("")}
          className="modal_notify_save"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bn  lu 30 t vo s tay.</p>
                <p className="notify-save-word">
                  Nng cp ti khon  lu t khng gii hn
                </p>
                <PrimaryButton
                  onClick={handleUpgrade}
                  className="btn_save_notify"
                >
                  Nng cp ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
      {expiredDay && (
        <MainModal
          show={type == "overload"}
          mascot={mochiNoti}
          handleClose={() => setShowModal("")}
          className="modal_notify_save"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bn  lu 50 t vo s tay.</p>
                <p className="notify-save-word">
                  Nng cp ti khon  lu t khng gii hn
                </p>
                <PrimaryButton
                  onClick={handleUpgrade}
                  className="btn_save_notify"
                >
                  Nng cp ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
    </>
  );
}

export default NotifyModal;
